.ms_sidemenu_inner {
    width: 80px;
    background-color: #fff;
    height: 100%;
    z-index: 1;
}

.ms_nav_close {
    width: 55px;
    height: 55px;
    text-align: center;
    line-height: 55px;
    position: absolute;
    right: -24px;
    top: 50%;
    background-color: #ffffff;
    border-radius: 100%;
    cursor: pointer;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    z-index: -1;
    box-shadow: 0px 0px 18px 2px rgba(4, 4, 4, 0.1);
}

.ms_nav_close i {
    color: #777777;
    font-size: 20px;
    position: absolute;
    top: 50%;
    right: 12px;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
}

.ms_sidemenu_wrapper.open_menu {
    width: 200px;
}

.ms_logo_open {
    display: none;
}

.ms_sidemenu_wrapper.open_menu .ms_logo_open {
    display: block;
}

.ms_sidemenu_wrapper.open_menu .ms_logo {
    display: none;
}

.ms_sidemenu_wrapper.open_menu .ms_sidemenu_inner {
    width: 200px;
}

.ms_sidemenu_wrapper.open_menu span.nav_text {
    opacity: 1;
    display: inline-block;
    visibility: visible;
    position: relative;
    left: auto;
    top: 0;
    background-color: transparent;
    vertical-align: bottom;
}

.ms_sidemenu_wrapper.open_menu a {
    text-align: left;
    padding: 9px 15px;
}

.ms_sidemenu_wrapper.open_menu span {
    display: inline-block;
}

.ms_sidemenu_wrapper.open_menu .ms_nav_close i {
    transform: translateY(-50%) rotate(180deg);
    -webkit-transform: translateY(-50%) rotate(180deg);
    -moz-transform: translateY(-50%) rotate(180deg);
    -ms-transform: translateY(-50%) rotate(180deg);
}

